import React, { Component, useState, useEffect } from "react";
import { useQuery, gql } from "@apollo/client";
import axios from "axios";
import { withRouter } from "react-router";

const initData = {
  pre_heading: "Explore",
  heading: "Exclusive Digital Assets",
  content:
    "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Laborum obcaecati dignissimos quae quo ad iste ipsum officiis deleniti asperiores sit.",
  btnText: "Load More",
};
const NftView = ({ src }) => {
  const [img, setImg] = useState(true);
  const loadMedia = (src) => {
    var img = new Image();
    img.onerror = () => {
      console.log(src);
      setImg(false);
    };
    img.src = src;
  };
  useEffect(() => {
    loadMedia(src);
  }, []);
  if (img) {
    return <img className="card-img-top" src={src} alt="" />;
  }
  return (
    <video
      autoPlay
      loop
      muted
      className="card-img-top"
      src={src}
      alt=""
    ></video>
  );
};

const NftImg = ({ src }) => {
  const [img, setImg] = useState(true);
  const loadMedia = (src) => {
    var img = new Image();
    img.onerror = () => {
      console.log(src);
      setImg(false);
    };
    img.src = src;
  };
  useEffect(() => {
    loadMedia(src);
  }, []);

  return (
    <img
      style={{ height: "auto", width: "300px" }}
      className="card-img-top"
      src={src}
      alt=""
    />
  );
};
class ExploreFour extends Component {
  _isMounted = 1;
  constructor(props) {
    super(props);
    this.getAllNft();
  }

  state = {
    initData: {},
    data: [],
    ImgUri: [],
    error: null,
    isLoaded: false,
    source: [],
    videoSrc: "",
    image: true,
    vid: true,
    imageUri: {},
    currentLoad: 0,
  };
  getAllNft = gql`
    {
      nftentities {
        name
        description
        id
        uri
        owner
        creator {
          id
        }
        sale {
          id
          price
        }
        auction {
          id
        }
      }
    }
  `;
  fetchImageObject = async () => {
    try {
      let resp;
      if (this._isMounted === 1) {
        this.state.data.forEach((nft) =>
          axios
            .get(`https://gateway.pinata.cloud/ipfs/${nft.uri}`)
            .then((resp) =>
              this.setState({
                source: {
                  ...this.state.source,
                  [nft.id]: `https://ipfs.io/ipfs/${resp.data.url}`,
                },
              })
            )
        );
      }

      if (this._isMounted === 1) {
        this.setState(() => ({
          isLoaded: true,
        }));
      }
    } catch (error) {
      console.log(error);
    }
  };

  getAllNft = async () => {
    const query = `
    {
        nftentities {
          name
          description
          id
          uri
          owner
          creator {
            id
          }
          sale {
            id
            price
          }
          auction {
            id
          }
        }
      }
    `;
    try {
      const res = await axios.post(
        "https://api.thegraph.com/subgraphs/name/vjbhandari61/saimart",
        {
          query,
        }
      );

      if (this._isMounted === 1) {
        this.setState(
          {
            isLoaded: true,
            data: res.data.data.nftentities,
            currentLoad:
              res.data.data.nftentities.length > 12
                ? 12
                : res.data.data.nftentities.length,
          },
          () => {
            this.fetchImageObject();
          }
        );
      }
    } catch (error) {
      this.setState(() => ({ error }));
      console.log(error);
    }
  };

  componentDidMount() {
    // this.fetchImageObject();

    if (this._isMounted === 1) {
      this.setState({
        initData: initData,
        // data: data,
        // source: source,
      });
    }

    this._isMounted = 1;
    // this.getAllNft();
  }
  componentWillUnmount() {
    this._isMounted = 0;
    this.setState = (state, callback) => {
      return;
    };
  }
  handleLoadMore = () => {
    if (this.state.currentLoad < this.state.data.length) {
      if (this.state.currentLoad + 4 <= this.state.data.length) {
        this.setState({ currentLoad: this.state.currentLoad + 4 });
      } else {
        this.setState({ currentLoad: this.state.data.length });
      }
    } else {
    }
  };
  render() {
    const { currentLoad } = this.state;
    return (
      <section className="explore-area load-more">
        <div className="container">
          <div className="row justify-content-center">
            <div className="col-12 col-md-8 col-lg-7">
              {/* Intro */}
              <div className="intro text-center">
                <span>{this.state.initData.pre_heading}</span>
                <h3 className="mt-3 mb-0">{this.state.initData.heading}</h3>
              </div>
            </div>
          </div>
          <div className="row items">
            {this.state.data.slice(0, currentLoad).map((item, idx) => {
              return (
                <div
                  key={`exf_${idx}`}
                  onClick={(e) =>
                    this.props.history.push(
                      `/details/${item.id}`,
                      this.state.data
                    )
                  }
                  className="col-12 col-sm-6 col-lg-3 mt-4"
                >
                  <div
                    style={{
                      height: "fit-content",
                      cursor: "pointer",
                      width: "274px",
                    }}
                    className="card"
                  >
                    {this._isMounted === 1 ? (
                      <div style={{ height: "180px" }} className="image-over">
                        <a>
                          <div
                            style={{
                              backgroundImage: `url(${
                                this.state.source[item.id]
                              })`,
                              height: "180px",
                              width: "100%",
                              backgroundRepeat: "no-repeat",
                              backgroundPosition: "center",
                              backgroundSize: "cover",
                            }}
                          ></div>
                          {/* <NftImg src={this.state.source[idx]} /> */}
                        </a>
                      </div>
                    ) : (
                      <span></span>
                    )}
                    <div className="image-over">
                      <img className="card-img-top" src={item.img} alt="" />
                    </div>
                    {/* Card Caption */}
                    <div className="card-caption col-12 p-0">
                      {/* Card Body */}
                      <div className="card-body">
                        <h5 className="mb-0">{item.name}</h5>
                        <div className="seller d-flex align-items-center my-3">
                          <span>Owned By</span>
                          <a href="/author">{/* <h6>{item.owner}</h6> */}</a>
                        </div>
                        <div className="card-bottom d-flex justify-content-between">
                          <span style={{ fontSize: "9px", fontWeight: "bold" }}>
                            {item.owner}
                          </span>
                          {/* <span>{item.count}</span> */}
                        </div>
                        {(!item?.sale && item?.auction && (
                          <a
                            className="btn btn-bordered-white btn-smaller mt-3"
                            onClick={() =>
                              this.props.history.push(
                                `/details/${item.id}`,
                                this.state.data
                              )
                            }
                          >
                            Bid
                          </a>
                        )) ||
                          (!item?.auction && item?.sale && (
                            <a
                              className="btn btn-bordered-white btn-smaller mt-3"
                              onClick={() =>
                                this.props.history.push(
                                  `/details/${item.id}`,
                                  this.state.data
                                )
                              }
                            >
                              Buy
                            </a>
                          )) ||
                          (!item?.sale && !item?.auction && (
                            <a
                              className="btn btn-bordered-white btn-smaller mt-3"
                              onClick={() =>
                                this.props.history.push(
                                  `/details/${item.id}`,
                                  this.state.data
                                )
                              }
                            >
                              View
                            </a>
                          )) ||
                          null}
                      </div>
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
          <div className="row">
            <div className="col-12 text-center">
              {currentLoad === this.state.data.length ? null : (
                <span
                  onClick={this.handleLoadMore}
                  className="btn btn-bordered-white mt-5"
                >
                  {this.state.initData.btnText}
                </span>
              )}
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default withRouter(ExploreFour);
